.projects {
    /* .text-zone{

    } */

    .card-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        justify-content: center;
        margin: 0 50%;

        > * {
            justify-self: center;
        }

        > *:nth-child(2n+1) {
            grid-column: 1/2;   
        }
    
        > *:nth-child(2n) {
            grid-column: 2/3;
        }
    
        > *:nth-child(2n+1):nth-last-of-type(1) {
            grid-column: 1/3;
        }
    }
}